* {
    margin: 0;
    box-sizing: border-box;
}


body {
    width: 100%;
    background-color: #e0dee0;
    display: flex;
    justify-content: center;
}

#root {
    width: 100%;
    display: flex;
    justify-content: center;
}

.app {
    width: 100%;
    min-height: 100vh;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    align-items: center;

    .results_body {
        width: 100%;
        display: flex;
        flex: 1;
        padding: 1% 2%;
        align-items: flex-start;
    }
}